import { createAction, createReducer } from "@reduxjs/toolkit";

const SEND_MESSAGE = 'SEND_MESSAGE';

const initState = {
    messagesData: [
        {id: 1, message: '', authorName: ''},
        {id: 2, message: '', authorName: ''},
        {id: 3, message: '', authorName: ''},
        {id: 4, message: '', authorName: ''},
    ],
    dialogsData: [
        {id: 1, userName: ''},
        {id: 2, userName: ''},
        {id: 3, userName: ''},
        {id: 4, userName: ''},
        {id: 5, userName: ''},
    ],
};

const sendMessageAction = createAction(SEND_MESSAGE);

const dialogReducer = createReducer(initState, (builder) => {
    builder
        .addCase(sendMessageAction, (state, action) => ({
            ...state,
            messagesData: [
                ...state.messagesData,
                {
                    id: Math.round(Math.random() * 1000),
                    message: action.payload,
                    authorName: ' '
                }
            ]
        }))
});

export { dialogReducer, sendMessageAction };